.startmeter{
	width:56vmin;
	height:28vmin;
	/* width:56px;
	height:28px; */
	position: relative;
	overflow: hidden;
    text-align: center;
    margin: auto;
	/* transform: translate(-50%, -50%); */
}

.startmeter .gauge-a{
	 z-index: 1;
	/* position: absolute; */
	background-color: #beef;
	width: 100%;
	height: 100%;
	/* top: 0%; */
	border-radius:250vmin 250vmin 0vmin 0vmin ;
	/* border-radius:250px 250px 0px 0px ; */
}


.startmeter .gauge-b{
	z-index: 3;
	position: absolute;
	background-color: #222;
	width: 75%;
	height: 75%;
    /* top: 75%; */
    bottom: 0;
	margin-left: 50%;
    margin-right: auto;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
	border-radius:250vmin 250vmin 0vmin 0vmin ;
	/* border-radius:250px 250px 0px 0px ; */
}
.startmeter .gauge-c{
	z-index: 2;
	position: absolute;
	background-color: #5664F9;
	width: 100%;
	height: 100%;
	/* top: 200px; */
	margin-left: auto;
	margin-right: auto;
	border-radius:0vmin 0vmin 200vmin 200vmin ;
     -webkit-transform-origin:center top;
             transform-origin:center top; 
     -webkit-transition: all 1.3s ease-in-out; 
     transition: all 1.3s ease-in-out; 
     /* transform: rotate(180deg); */
}


