#sticky{
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    height: 5vh;
    width: 100%;
    background: #fff;
    opacity: 0.6;
   filter: alpha(opacity=50);
   z-index: 100;
   text-align: center;
}

